// Main SCSS file
// Check the README file for information on how to edit
// Edit your 'resets', 'utilities' and 'universal' styles in the _sass directory

@import
  '_resets',
  '_fontface';

// Sass variables
// Sass global variables must come BEFORE partials that use them and declared on the 'main.scss' file
$primary-color                   : #333333;
$primary-shade                   : #aeaeae;
$secondary-color                 : #383231;
$secondary-shade                 : #999999;
$accent-color                    : #e9a547;
$body-font-family                : 'Noto Serif KR', serif;;
$heading-font-family             : 'Eczar', serif;;
$heading-default-bottom-margin   : 2rem;
$gallery-padding                 : 1.5rem;

// Sass partials
// Add necessary partials from here onwards
@import
  '_mixins',
  '_utility',
  '_universal',
  '_padding',
  '_containers',
  '_buttons',
  '_nav',
  '_blocks',
  '_animate',
  '_index',
  '_footer',
  '_media-queries';



  // Alternatively, write your CSS/SCSS from here onwards
